/* styles.css */

.page-entering {
  opacity: 0;
  transform: translateY(20px);
}

.page-entered {
  opacity: 1;
  transform: translateY(0);
}

.page-exiting {
  opacity: 0;
  transform: translateY(20px);
}

.page-exited {
  display: none;
}


@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}